.qualification {
  &__content {
    display: none;
  }
  &__tabs {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: $mb-2;
  }

  &__icon {
    font-size: 1.8rem;
    margin-right: $mb-0-25;
  }

  &__data {
    display: grid;
    grid-template-columns: 1fr max-content 1fr;
    column-gap: 1.5rem;
  }

  &__title {
    font-size: $normal-font-size;
    font-weight: $font-medium;
  }
  &__subtitle {
    display: inline-block;
    font-size: $small-font-size;
    margin-bottom: $mb-1;
  }
  &__calendar {
    font-size: $smaller-font-size;
    color: $text-color-light;
  }

  &__rounder {
    display: inline-block;
    width: 13px;
    height: 13px;
    background-color: $first-color;
    @include radius(50%);
  }

  &__line {
    display: block;
    width: 1px;
    height: 100%;
    background-color: $first-color;
    @include custom_translate(6px, -7px);
  }

  &__active {
    display: block;
  }
}
