.services {
  &__container {
    gap: 1.5rem;
    grid-template-columns: repeat(2, 1fr);
  }

  &__content {
    position: relative;
    background-color: $container-color;
    padding: 3.5rem 0.5rem 1.25rem 1.5rem;
    @include radius(0.25rem);
    box-shadow: 0 2px 4px rgba($color: #000000, $alpha: 0.15);
    @include custom_transition(300ms);

    &:hover {
      box-shadow: 0 4px 8px rgba($color: #000000, $alpha: 0.15);
    }
  }
}
