@mixin radius($radius: 1rem) {
  border-radius: $radius;
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  -ms-border-radius: $radius;
  -o-border-radius: $radius;
}

@mixin custom_transition($duration: 400ms) {
  transition: $duration ease-out;
  -webkit-transition: $duration ease-out;
  -moz-transition: $duration ease-out;
  -ms-transition: $duration ease-out;
  -o-transition: $duration ease-out;
}

@mixin custom_translateX($y: 1rem) {
  transform: translateX($y);
  -webkit-transform: translateX($y);
  -moz-transform: translateX($y);
  -ms-transform: translateX($y);
  -o-transform: translateX($y);
}

@mixin custom_translateY($y: 1rem) {
  transform: translateY($y);
  -webkit-transform: translateY($y);
  -moz-transform: translateY($y);
  -ms-transform: translateY($y);
  -o-transform: translateY($y);
}

@mixin custom_translate($y: 1rem, $x: 1rem) {
  transform: translate($y, $x);
  -webkit-transform: translate($y, $x);
  -moz-transform: translate($y, $x);
  -ms-transform: translate($y, $x);
  -o-transform: translate($y, $x);
}

@mixin rotation($y) {
  transform: rotate($y);
  -webkit-transform: rotate($y);
  -moz-transform: rotate($y);
  -ms-transform: rotate($y);
  -o-transform: rotate($y);
}

// MEDIA QUERY MANAGER

// 0 - 600 PHONE
// 600 - 900 TABLET PORTRAIT
// 900 - 1200 TABLET LANDSCAPE
// [1200 - 1800] DESKTOP
// +1800 BIG DESKTOP

@mixin respond-phone {
  @media (max-width: 899px) {
    @content;
  }
}

@mixin respondUI($breakpoint) {
  @if $breakpoint==sm-phone {
    @media (max-width: 350px) {
      @content;
    }
  }
  @if $breakpoint==phone {
    @media (min-width: 600px) {
      @content;
    }
  }
  @if $breakpoint==tab-port {
    @media (min-width: 900px) {
      @content;
    }
  }
  @if $breakpoint==tab-land {
    @media (min-width: 1200px) {
      @content;
    }
  }
  @if $breakpoint==big {
    @media (min-width: 1800px) {
      @content;
    }
  }
}
