.home {
  &__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;

    &-icon {
      font-size: 1.25rem;
      color: $first-color;

      &:hover {
        color: $first-color-alt;
      }
    }
  }

  &__blob {
    width: 200px;
    fill: $first-color;

    &-img {
      width: 170px;
    }
  }

  &__data {
    grid-column: 1/3;
  }

  &__scroll {
    display: none;
    &-arrow,
    &-mouse {
      font-size: 2rem;
    }

    &-name {
      font-size: $small-font-size;
      color: $title-color;
      font-weight: $font-medium;
      margin-right: $mb-0-25;
      white-space: nowrap;
    }
  }
}
