.services {
  &__icon {
    display: block;
    font-size: 1.5rem;
    color: $first-color;
    margin-bottom: $mb-1;
  }

  &__title {
    font-size: $h3-font-size;
    margin-bottom: $mb-1;
    font-weight: $font-medium;
  }

  &__modal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background-color: rgba($color: #000000, $alpha: 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 1rem;
    z-index: $z-modal;
    opacity: 0;
    visibility: hidden;
    @include custom_transition(300ms);

    &-content {
      position: relative;
      background-color: $container-color;
      padding: 1rem;
      @include radius(0.5rem);
    }

    &-services {
      row-gap: 1rem;
    }

    &-service {
      display: flex;
    }
    &-title {
      font-size: $h3-font-size;
      font-weight: $font-medium;
      margin-bottom: $mb-1-5;
    }
    &-close {
      position: absolute;
      top: 1rem;
      right: 1rem;
      font-size: 1.5rem;
      color: $first-color;
      cursor: pointer;
    }
    &-active {
      opacity: 1;
      visibility: visible;
    }
  }
}
