.skills {
  &__container {
    row-gap: 0;
  }

  &__header {
    display: flex;
    align-items: center;
    margin-bottom: $mb-2-5;
    cursor: pointer;
  }

  &__icon,
  &__arrow {
    font-size: 2rem;
    color: $first-color;
  }

  &__icon {
    margin-right: $mb-0-75;
  }

  &__arrow {
    margin-left: auto;
    @include custom_transition(400ms);
  }

  &__title {
    font-size: $h3-font-size;
  }

  &__subtitle {
    font-size: $small-font-size;
    color: $text-color-light;
  }

  &__titles {
    display: flex;
    justify-content: space-between;
    margin-bottom: $mb-0-5;
  }

  &__name {
    font-size: $normal-font-size;
    font-weight: $font-medium;
  }
  &__bar,
  &__percentage {
    height: 5px;
    @include radius(0.25rem);
  }
  &__bar {
    background-color: $first-color-lighter;
  }
  &__percentage {
    display: block;
    background-color: $first-color;
  }

  &__list {
    row-gap: 1.5rem;
    padding-left: 2.7rem;
  }
  //   frontend

  &__html {
    width: 90%;
  }

  &__css {
    width: 80%;
  }
  &__javascript {
    width: 60%;
  }
  &__flask {
    width: 40%;
  }
  &__android {
    width: 40%;
  }
  &__scss,
  &__nodejs,
  &__flutter,
  &__react {
    width: 70%;
  }
  &__postgres {
    width: 50%;
  }

  &__open {
    height: max-content;
    margin-bottom: $mb-2-5;
  }
  &__close {
    display: none;
  }

  &__arrow-rotate {
    @include rotation(180deg);
  }
}
