.about {
  &__img {
    width: 200px;
    @include radius(50%);
    justify-self: center;
    align-self: center;
  }

  &__description {
    text-align: center;
    margin-bottom: $mb-2-5;
  }

  &__info {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: $mb-2-5;

    &-title {
      font-size: $h2-font-size;
      font-weight: $font-semi-bold;
      color: $title-color;
    }
    &-name {
      font-size: $smaller-font-size;
    }

    &-name,
    &-title {
      display: block;
      text-align: center;
    }
  }
}
