.button {
  display: inline-block;
  background-color: $first-color;
  color: #fff;
  padding: 1rem;
  @include radius(0.5rem);
  font-weight: $font-medium;

  &:hover {
    background-color: $first-color-alt;
  }

  &__icon {
    font-size: 1.25rem;
    margin-left: $mb-0-5;
    @include custom_transition(300ms);
  }

  &--flex {
    display: inline-flex;
    align-items: center;
  }

  &--small {
    padding: 0.75rem 1rem;
  }

  &--link {
    padding: 0;
    background-color: transparent;
    color: $first-color;
    &:hover {
      background-color: transparent;
      color: $first-color-alt;
    }
  }
}
// HOME BTN
.home {
  &__scroll {
    &-button {
      color: $first-color;
      @include custom_transition(300ms);
      &:hover {
        @include custom_translateY(0.25rem);
      }
    }
  }
}

// ABOUT BTN

.about {
  &__buttons {
    display: flex;
    justify-content: center;
  }
}

// QUALIFICATION BTN

.qualification {
  &__button {
    font-size: $h3-font-size;
    font-weight: $font-medium;
    cursor: pointer;
    &-active {
      color: $first-color;
    }
    &:hover {
      color: $first-color;
    }
  }
}

// SERVICES BTN
.services {
  &__button {
    cursor: pointer;
    font-size: $small-font-size;
    &:hover .button__icon {
      @include custom_translateX(0.25rem);
    }
  }
}
