// SMALL PHONE
@include respondUI(sm-phone) {
  .container {
    margin-left: $mb-1;
    margin-right: $mb-1;
  }
  //   HOME
  .home {
    &__content {
      grid-template-columns: 0.25fr 3fr;
    }

    &__blob {
      width: 180px;
    }
  }

  // SKILLS
  .skills {
    &__title {
      font-size: $normal-font-size;
    }
  }
  // qualification
  .qualification {
    &__data {
      gap: 0.5rem;
    }
  }

  // qualification
  .services {
    &__container {
      grid-template-columns: max-content;
      justify-content: center;
    }

    &__content {
      padding-right: 5rem;
    }

    &__modal {
      padding: 0 0.5rem;
    }
  }
}

//  PHONE

@include respondUI(phone) {
  //   HOME
  .home {
    &__content {
      grid-template-columns: max-content 1fr 1fr;
    }

    &__data {
      grid-column: initial;
    }

    &__img {
      order: 1;
      justify-self: center;
    }
  }

  // ABOUT
  .about {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // SKILLS
  .skills {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  // QUALIFICATION
  .qualification {
    &__sections {
      grid-template-columns: 0.6fr;
      justify-content: center;
    }
  }
  .portfolio {
    &__img {
      width: 250px;
    }

    &__content {
      grid-template-columns: repeat(2, 1fr);
      justify-content: space-around;
      align-items: center;
      column-gap: 5rem;
      &:nth-child(even) {
        direction: rtl;
      }
    }
  }

  // contact
  .contact {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // footer
  .footer {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
}

// TABLET

@include respondUI(tab-port) {
  .container {
    margin-left: auto;
    margin-right: auto;
  }

  body {
    margin: 0 !important;
  }

  .section {
    padding: 6rem 0 2rem;
    &__subtitle {
      margin-bottom: 4rem;
    }
  }

  .header {
    top: 0;
    bottom: initial;
  }

  .header,
  .main {
    padding: 0 1rem;
  }
  //   NAVBAR
  .nav {
    height: 4.5rem;
    column-gap: 1rem;

    &__icon,
    &__close,
    &__toggle {
      display: none;
    }

    &__list {
      display: flex;
      column-gap: 2rem;
    }
    &__menu {
      margin-left: auto;
    }
  }
  //   HOME
  .home {
    &__container {
      row-gap: 5rem;
    }
    &__content {
      padding-top: 5.5rem;
      column-gap: 2rem;
      grid-template-columns: max-content 1fr 1fr;
    }

    &__data {
      grid-column: initial;
    }

    &__img {
      order: 1;
      justify-self: center;
    }

    &__blob {
      width: 270px;
    }
    &__scroll {
      display: block;

      &-button {
        margin-left: 3rem;
      }
    }
  }

  // ABOUT
  .about {
    &__container {
      column-gap: 5rem;

      grid-template-columns: repeat(2, 1fr);
    }
    &__img {
      width: 350px;
    }
    &__description {
      text-align: initial;
    }
    &__info {
      justify-content: space-between;
    }
    &__buttons {
      justify-content: space-between;
    }
  }

  // SKILLS
  .skills {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  // QUALIFICATION
  .qualification {
    &__sections {
      grid-template-columns: 0.5fr;
      justify-content: center;
    }
  }
  // SERVICES

  .services {
    &__container {
      grid-template-columns: repeat(2, 1fr);
      justify-content: center;
    }

    &__icon {
      font-size: 2rem;
    }

    &__content {
      padding: 6rem 0 2rem 2.5rem;
    }

    &__modal {
      padding: 0 2rem;
      &-content {
        width: 450px;
      }
    }
  }

  .portfolio {
    &__img {
      width: 250px;
    }

    &__content {
      grid-template-columns: repeat(2, 1fr);
      justify-content: space-around;
      align-items: center;
      column-gap: 5rem;
      &:nth-child(even) {
        direction: rtl;
      }
    }
  }

  // contact
  .contact {
    &__container {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  // footer
  .footer {
    &__container {
      padding: 0 1rem;
      grid-template-columns: repeat(3, 1fr);
    }
    &__bg {
      padding: 3rem 0 3.5rem;
    }

    &__links {
      grid-template-columns: repeat(3, 1fr);
      column-gap: 2rem;
    }

    &__socials {
      justify-self: flex-end;
    }
  }
}

@include respondUI(tab-land) {
  .container {
    max-width: 70vw;
    margin-left: auto;
    margin-right: auto;
  }
  .header,
  .main {
    padding: 0;
  }

  //   HOME
  .home {
    &__content {
      padding-top: 5.5rem;
    }
    &__blob {
      width: 320px;
    }
  }

  // footer
  .footer {
    &__container {
      padding: 0;
    }
  }
}
