.portfolio {
  &__img {
    width: 265px;
    @include radius(0.5rem);
    justify-self: center;
  }
  &__title {
    font-size: &h3-font-size;
    margin-bottom: $mb-0-5;
  }

  &__description {
    font-size: &h3-font-size;
    margin-bottom: $mb-0-75;
  }
  &__button:hover .button__icon {
    @include custom_translateX(0.25rem);
  }
}
